<template>
  <div id="login" class="flex flex_pack_justify">
    <div class="login_carousel">
     <div class="login_logo">
       <a href="/" title="孚盟MX" class="reg_logo">
         <i class="iconfont icon-logo"></i>
         <span>孚盟软件旗下品牌</span>
       </a>
     </div>
      <div class="login_carousel_item">
        <el-carousel>
          <el-carousel-item v-for="item in carouselList" :key="item">
            <img :src="item" alt="">
          </el-carousel-item>
        </el-carousel>
      </div>
    </div>
    <div class="login-box">
      <div class="login_sign"><el-button>注册新企业</el-button></div>
      <div class="login-box-from">
        <div class="login_title">登录</div>
        <el-form :model="loginForm" :rules="rules" ref="loginForm" class="demo-ruleForm"  :inline="true"  label-width="70px">
          <el-form-item label="用户名" class="username" prop="username" required>
            <el-input  v-model="loginForm.username" placeholder="请输入用户名" size="medium">
            </el-input>
          </el-form-item>
          <el-form-item class="" label="密码" prop="password" required>
            <el-input class="password" v-model="loginForm.password" placeholder="请输入密码" size="medium">
            </el-input>
          </el-form-item>
          <el-form-item label="语言" prop="value">
            <el-select
              v-model="value"
              filterable
              allow-create
              default-first-option
              placeholder="请选择文章标签">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <div class="reset" @click="resets('loginForm')"></div>
        </el-form>
        <div class="login_check flex flex_pack_justify">
          <div><el-checkbox v-model="checked">记住密码</el-checkbox></div>
          <div class="login_forget">忘记密码</div>
        </div>
        <div class="submit"><el-button type="danger" @click="submitForm('loginForm')">登录</el-button></div>
      </div>
      <div class="login_app">
        <ul class="flex flex_pack_justify">
          <li  @mouseenter="mouseenter($event)"  @mouseleave="mouseleave($event)">
            <div class="tip">
              <i class="i1"></i>
              <p>Windows</p>
            </div>
            <div class="todo" style="display: none">
              <i class="i1_1"></i>
              <p>Windows客户端</p>
            </div>
          </li>
          <li @mouseenter="mouseenter($event)"  @mouseleave="mouseleave($event)">
            <div class="tip">
              <i class="i2"></i>
              <p>Mac</p>
            </div>
            <div class="todo" style="display: none">
              <i class="i1_1"></i>
              <p>Mac</p>
            </div>
          </li>
          <li @mouseenter="mouseenter($event)"  @mouseleave="mouseleave($event)">
            <div class="tip">
              <i class="i3"></i>
              <p>Windows</p>
            </div>
            <div class="todo" style="display: none">
              <i class="i1_1"></i>
              <p>Iphone客户端</p>
            </div>
          </li>
          <li @mouseenter="mouseenter($event)"  @mouseleave="mouseleave($event)">
            <div class="tip">
              <i class="i4"></i>
              <p>Windows</p>
            </div>
            <div class="todo" style="display: none">
              <i class="i1_1"></i>
              <p>Android客户端</p>
            </div>
          </li>
        </ul>
      </div>
      <div class="login_about">
        Copyright © 2019 Shanghai Fumasoft co.,Ltd. 赣ICP备18002562号-4
      </div>
    </div>
  </div>
</template>

<script>
  import ElFormItem from '../../../node_modules/element-ui/packages/form/src/form-item'
export default {
  name: 'login',
  components: {
    ElFormItem
  },
  data () {
    let letterRule = (rule, value, callback) => {
      let reg = /^[a-zA-Z0-9_-]{4,18}$/
      if (value === '') {
        callback(new Error('输入内容不能为空'))
      } else if (!reg.test(value)) {
        callback(new Error('请输入字母、数字、下划线'))
      } else {
        callback()
      }
    }
    return {
      loading: false, // 登陆状态
      loginForm: {
        username: 'zhengyawei',
        password: '123456',
        code: ''
      },
      checked: false,
      carouselList: ['https://sf.fumamx.com/img/orig/74,3bd737c5f266a6', 'https://sf.fumamx.com/img/orig/4,4b3056851482', 'https://sf.fumamx.com/img/orig/69,327aa420fd1eb1'],
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 2, max: 18, message: '长度在 2 到 18 个字符', trigger: 'blur' },
          { validator: letterRule, trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 16, message: '长度在 6 到 16 个字符', trigger: 'blur' },
          { validator: letterRule, trigger: 'blur' }
        ]
      },
      options: [{
        value: '中文简体',
        label: '中文简体'
      }, {
        value: '中文繁体',
        label: '中文繁体'
      }, {
        value: 'English',
        label: 'English'
      }],
      value: []
    }
  },
  methods: {
    submitForm (formName) {
      this.$router.push({
        path: '/home'
      })
    },
    resets (formName) {
      this.$refs[formName].resetFields()
    },
    mouseenter (event) {
      console.log(event.currentTarget.getElementsByClassName('todo'))
      event.currentTarget.getElementsByClassName('todo')[0].style.display = 'block'
      event.currentTarget.getElementsByClassName('tip')[0].style.display = 'none'

    },
    mouseleave () {
      event.currentTarget.getElementsByClassName('todo')[0].style.display = 'none'
      event.currentTarget.getElementsByClassName('tip')[0].style.display = 'block'
    }
  },
  computed: {},
  mounted () {
  },
  created () {
  }
}
</script>

<style lang="less">
  @bg_color: #3E3E3E;
  #login {
    width: 100%;
    height: 100%;
    position: relative;
    .login_carousel {
      width: 62%;
      height: 100%;
      .login_carousel_item {
        height: 100%;
        .el-carousel--horizontal {
          height: 100%;
          .el-carousel__container {
            height: 100%;
          }
        }
      }
      .login_logo {
        width: 138px;
        height: 58px;
        line-height: 50px;
        position: absolute;
        left: 3%;
        top: 25px;
        z-index: 9;
        color: #fff;
        overflow: hidden;
        i {
          font-size: 133px;
          font-family: "iconfont" !important;
          font-style: normal;
          -webkit-font-smoothing: antialiased;
        }
        icon-logo:before {
          content: "\e66c";
        }
      }
    }
    .login-box {
      width: 38%;
      position: relative;
      .login_sign {
        padding-top:25px ;
        padding-right: 40px;
        text-align: right;
        .el-button {
          span {
            font-size: 14px;
          }
        }
      }
      .login-box-from{
        box-sizing: border-box;
        width: 360px;
        margin: 0 auto;
        margin-top: -5px;
        .login_title {
          color: #d0021b;
          font-size: 32px;
          line-height: normal;
          margin-bottom: 30px;
          font-weight: 400;
        }
        .el-form {
          width: 100%;
          .el-form-item {
             margin-bottom: 15px;
            width: 100%;
            position: relative;
            .el-form-item__label {
              text-align: left;
            }
            .el-form-item__label:nth-child(1)::before {
              position: absolute;
              left: 45px;
            }
            .el-form-item__content {
              width: calc(100% - 70px);
              .el-select {
                width: 100%;
              }
            }
          }
          .submit {
            width: 90px;
            height: 30px;
            position: absolute;
            bottom: 185px;
            right: 150px;
          }
          .reset {
            width: 90px;
            height: 30px;
            position: absolute;
            bottom: 130px;
            right: 150px;
          }
        }
        .login_check {
          margin-bottom: 28px;
          .login_forget {
            font-size: 14px;
            color: #606266;
          }
        }
        .el-form-item__content .el-input-group{
          vertical-align:inherit;
        }
        .el-button {
          width: 100%;
          color: #fff;
          background-color: #d0021b;
          border-color: #d0021b;
          span{
            font-size: 14px;
          }
        }
      }
    }
    .login_app {
      padding: 30px 50px;
       ul {
         li {
           width: 98px;
           height: 100px;
           border: 1px solid #ddd;
           border-radius: 4px;
           color: #303133;
           position: relative;
           text-align: center;
           overflow: hidden;
           line-height: 1;
           div {
             height: 100%;
             i {
               display: inline-block;
               width: 48px;
               height:48px;
               margin-top: 15px;
             }
             p {
               color: #303133;
               position: absolute;
               left: 0;
               right: 0;
               bottom: 8px;
               font-size: 12px;
             }
             .i1_1 {
               background: url("../../assets/img/login/xiazai.png");
             }
             .i1 {
               background: url("../../assets/img/login/windows-fill.png");
             }
             .i2 {
               background: url("../../assets/img/login/Appstore-.png");
             }
             .i3 {
               background: url("../../assets/img/login/mac.png");
             }
             .i4 {
                background: url("../../assets/img/login/android.png");
              }
           }
         }
       }
    }
    .login_about {
      text-align: center;
      font-size: 12px;
      color: #303133;
    }
  }
  .el-select-dropdown__item {
    font-size: 14px;
    padding: 8px 10px;
    margin-bottom: 1px;
    position: relative;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #48576a;
    height: 36px;
    line-height: 1.5;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    cursor: pointer;
  }
  .el-select-dropdown__item span {
    font-size: 14px;
  }
  .el-select-dropdown__item.hover, .el-select-dropdown__item:hover {
    background: #FCF2F3;
    color: #d0021b;
  }
</style>



